export default class Example extends PureComponent {
	constructor(props) {
    super(props);
    this.state = {
		roomNumber: 0
    };
  }
	
    handleBarClick = (e) => {
		console.log(e)
		this.props.handleBarClick(Number(e.activeLabel.split(' ')[0]))
    };

    render() {
		const data = this.props.data
		.map((room, index)=>{
			return {
				name: `${index+1} `, 
				attend: room.filter(soldier => {
					return soldier.eveningAttend===true && soldier.status===""
				}).length, 
				abscent: room.filter(soldiers => soldiers.eveningAttend!==true && soldiers.status==="").length,
				uncompleted: room.filter(soldiers => soldiers.status==="").length,
		}});
		
        return (
            <React.Fragment>
				<Title> 4    </Title>
                <ResponsiveContainer>
                    <BarChart
                        width={900}
                        height={300}
                        data={data}
                        margin={{
                            top: 20,
                            right: 30,
                            left: 20,
                            bottom: 5
                        }}
						onClick={this.handleBarClick}
                    >
						<CartesianGrid strokeDasharray="3 3" />
                        <XAxis dataKey="name" />
                        <YAxis hide />
                        <Tooltip />
                        <Legend />
                        <Bar
                            dataKey="attend"
                            stackId="a"
                            fill="#0088fe"
                            animationEasing="ease-in-out"
							
                        >
							<LabelList dataKey="attend" position="top"  />
						</Bar>
						<Bar
                            dataKey="abscent"
                            stackId="b"
                            fill="#ffbb28"
                            animationEasing="ease-in-out"
                            
                        >
							<LabelList dataKey="abscent" position="top"  />
						</Bar>
						<Bar
                            dataKey="uncompleted"
                            stackId="c"
                            fill="#8a8a8a"
                            animationEasing="ease-in-out"
                            
                        >
							<LabelList dataKey="uncompleted" position="top"  />
						</Bar>
                    </BarChart>
                </ResponsiveContainer>
            </React.Fragment>
        );
    }
}