import React from "react";
import { render } from "react-dom";
import { makeData, Logo, Tips } from "./Utils";

// Import React Table
import ReactTable from "react-table";
import "react-table/react-table.css";
import DateRangePicker from 'react-daterange-picker'


export default class MySoldiersTable extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: makeData(),
	  dateToggle: false,
    };
  }
  render() {
    const { data } = this.state;
    return (
      <div>
        <ReactTable
          data={data}
		  filterable
          defaultFilterMethod={(filter, row) =>
            String(row[filter.id]) === filter.value}
          columns={[{
      Header: ' ',
      columns: [{
        Header: '',
        accessor: 'date',
		filterMethod: (filter, row) =>
                    row[filter.id].startsWith(filter.value)
        ,
		Cell: row => (
			<div style={{textAlign: 'center'}}>
				{row.value}	
			</div>
		)
      }, {
        Header: '',
        accessor: 'class',
		filterMethod: (filter, row) => {
                    if (filter.value === "all") {
                      return true;
                    }
                    else if (filter.value === "") {
                    	return row[filter.id] === "";
                    }
					else if (filter.value === "") {
						return row[filter.id] === "";
					}
					else if (filter.value === "") {
                      return row[filter.id] === "";
                    }
                    return row[filter.id] === "";
                  },
        Filter: ({ filter, onChange }) => (
          <select
            onChange={event => onChange(event.target.value)}
            style={{ width: "100%" }}
            value={filter ? filter.value : "all"}
          >
            <option value="all"></option>
            <option value=""></option>
            <option value=""></option>
			<option value=""></option>
			<option value=""></option>
          </select>),
		Cell: row => (
			<div style={{textAlign: 'center'}}>
				{row.value}	
			</div>
		)
      }, {
        Header: '',
        id: 'lastName',
        accessor: "name",
		filterMethod: (filter, row) =>
                    row[filter.id].startsWith(filter.value)
        ,
		  Cell: row => (
			<div style={{textAlign: 'center'}}>
				{row.value}	
			</div>
		)
      }, {
        Header: '',
        accessor: 'militaryNumber',
		filterMethod: (filter, row) =>
                    row[filter.id].startsWith(filter.value)
        ,
		Cell: row => (
			<div style={{textAlign: 'center'}}>
				{row.value}	
			</div>
		)
      }]
    }, {
      Header: '   ',
      columns: [{
        Header: ' ',
        accessor: 'workTypes',
		filterMethod: (filter, row) =>
                    row[filter.id].startsWith(filter.value)
        ,
		  Cell: ({row, original}) => (
			<div style={{textAlign: 'center'}}>
				  {original.status===""? "" : original.workTypes}
			</div>
		)
      }, {
        Header: ' ',
        accessor: 'workTimes',
		filterMethod: (filter, row) =>
                    row[filter.id].startsWith(filter.value)
        ,
		  Cell: ({row, original}) => (
			<div style={{textAlign: 'center'}}>
				  {original.status===""? "" : original.workTimes}
			</div>
		)
      }, {
        Header: ' ',
        accessor: 'status',
		filterMethod: (filter, row) => {
                    if (filter.value === "all") {
                      return true;
                    }
                    if (filter.value === "true") {
                      return row[filter.id] === "";
                    }
                    return row[filter.id] === "";
                  },
        Filter: ({ filter, onChange }) => (
          <select
            onChange={event => onChange(event.target.value)}
            style={{ width: "100%" }}
            value={filter ? filter.value : "all"}
          >
            <option value="all"></option>
            <option value="true"></option>
            <option value="false"></option>
          </select>),
    	Cell: row => (
			<div style={{textAlign: 'center'}}>
				<span>
					<span 
						style={{
							height: '10px', 
							width: '10px', 
							backgroundColor:  row.value === '' ? '#57d500' :'#ff2e00',
							borderRadius: '50%', 
							display: 'inline-block',
							transition: 'all .3s ease ease-out',
							}} 
						class="dot">
					</span>
					{row.value === '' ? ' ' : ' ' }
				</span>
			</div>
        )
      }]
    }]}
          defaultPageSize={10}
          className="-striped -highlight"
		  previousText=" "
   		  nextText=" "
        />
        <br />
      </div>
    );
  }
}